.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.ballContainer{
  background-color: rgb(47, 13, 66);
  border-radius: 50%;
  width: 400px;
  height: 400px;
  margin: 15px auto 15px auto;
  text-align: center;
  cursor: pointer;
  color: white;
  padding: 100px;
   box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30),
   0 15px 12px rgba(0, 0, 0, 0.22);
  
}
.ballTeazer{
  
  font-weight: bold;
  font-size: 80px;
  margin-top: 100px;
}
.ballWish{
  font-size: 30px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
